<template>
  <div id="addressedit">
    <div id="wrap">
      <ul>
        <li style="text-align: center; padding: 1rem; color: #666;" v-show="!allAddress.length">
          请添加地址信息
        </li>
        <li v-for="(item, index) in allAddress">
          <div class="line"></div>
          <div class="content">
            <div class="top">
              <p class="name_phone">{{item.pname}}<span>{{item.phone}}</span></p>
              <p class="address">{{item.area.area}}{{item.area.city.city}}{{item.area.city.province.province}}{{item.addressinfo}}</p>
            </div>
            <div class="line"></div>
            <div class="bottom">
              <div class="left" @click="toMoren(item)">
                <span class="select-wrap" :class="{active:item.status}">
                  <span class="iconfont icon-6gou" :class="{active:item.status}"></span>
                </span>
                设为默认
              </div>
              <div class="right">
                <span @click="editAdd(item)"><span class="iconfont "></span>编辑</span>
                <span @click="deleteAdd(item)"><span class="iconfont "></span>删除</span>
              </div>
            </div>
          </div>
          <div class="line"></div>
        </li>
        <mt-button size="large" type="default" @click="goback()">返&nbsp;回</mt-button>
      </ul>
    </div>
    <router-link tag="div" to="/addaddress/0" class="add">
      <mt-button size="large" type="danger">新增收货地址</mt-button>
    </router-link>
  </div>
</template>
<script type="text/javascript" >
  export default {
    created () {
      this.allAddress = this.$store.state.allData.qbuid
      for (var i = 0; i < this.allAddress.length; i++) {
        this.allAddress[i].status = Number(this.allAddress[i].status)
      }
    },
    data () {
      return {
        value: true,
        allAddress: []
      }
    },
    methods: {
      editAdd (item) {
        this.$store.dispatch('editAddress', item)
        this.$router.push({ path: '/addaddress/1' })
      },
      deleteAdd (item) {
        this.allAddress.splice(this.allAddress.indexOf(item), 1)
      },
      toMoren (item) {
        for (var i = 0; i < this.allAddress.length; i++) {
          this.allAddress[i].status = 0
        }
        item.status = 1
      },
      goback () {
        this.$router.go(-1)
      }
    }
  }
</script>
<style lang="less">
  #addressedit{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    color: #333;
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    #wrap{
      flex: 1;
      padding-top: 0.5rem;
      background: #f4f4f4;
      >ul{
        height: 100%;
        overflow: scroll;
        >li{
          background: white;
          margin-bottom: 0.5rem;
          color: #666;
          >.content{
            padding-left: 0.5rem;
            >.top{
              padding: 0.5rem 0.5rem 0.4rem 0;
              >.name_phone{
                font-size: 0.9rem;
                color: #333;
                line-height: 1.2rem;
                >span{
                  float: right;
                }
              }
              >.address{
                font-size: 0.8rem;
                line-height: 1.7rem;
              }
            }
            >.bottom{
              height: 2rem;
              display: flex;
              font-size: 0.8rem;
              >.left{
                line-height: 2rem;
                >.select-wrap{
                  display: inline-block;
                  width: 1rem;
                  height: 1rem;
                  border: 1px solid #ddd;
                  border-radius: 50%;
                  transition: all 0.2s;
                  vertical-align: middle;
                  &.active{
                    border-color: #e72755;
                    background-color: #e72755;
                  }
                  >.iconfont{
                    opacity: 0;
                    font-size: 1rem;
                    color: #fff;
                    transition: all 0.2s;
                    position: relative;
                    top: -0.4rem;
                    &.active{
                      opacity: 1;
                    }
                  }
                }
              }
              >.right{
                flex: 1;
                text-align: right;
                padding-right: 0.5rem;
                line-height: 2rem;
              }
            }
          }
        }
      }
    }
    .add{
      padding: 0.5rem;
      background: #f4f4f4;
    }
    .mint-button--danger{
      font-size: 0.8rem;
      height: 2.5rem;
    }
    .mint-button--default{
      height: 2.5rem;
      background: #fff;
    }
  }
</style>



