<template>
  <div id="indexpage" :class="{right: isRight}">
    <div id="wrap">
      <div>
        <swiper :options="swiperOption">
          <swiper-slide v-for="(item, index) in turnimage">
            <div>
              <img @click="slideTo(item)" :src="item.imagePath" alt="">
            </div>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <div class="store-state">
          <div class="store-img">
          </div>
          <div class="store-name">
            <p>宝妈陶宝阁</p>
            <p style="font-size: 0.75rem;">微信昵称：天线宝宝ninsd</p>
          </div>
        </div>
        <div class="wrap">
          <hot></hot>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import Hot from '@/components/Hot'
  import IScroll from '../../static/iscroll-lite.js'

  export default {
    name: 'indexpage',
    components: {
      Hot
    },
    mounted () {
      setTimeout(() => {
        var myScroll = new IScroll('#wrap', {
          click: true
        })
      }, 300)
    },
    data () {
      return {
        swiperOption: {
          autoplay: 3000,
          setWrapperSize: true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          mousewheelControl: true,
          observeParents: true,
          loop: true
        }
      }
    },
    computed: {
      isRight () {
        return this.$store.state.isRight
      },
      turnimage () {
        return this.$store.state.allData.tic.turnimage
      }
    },
    methods: {
      slideTo (item) {
        let goodDetail = this.$store.state.allData.gc.getgood
        let goodImgs = this.$store.state.allData.gc.goodimage
        this.$store.dispatch('changeNowGood', { goodDetail, goodImgs })
        this.$router.push({ path: '/home/commodity' })
      }
    }
  }
</script>

<style lang="less">
  #indexpage {
    overflow: hidden;
    >#wrap{
      height: 100%;
      overflow: hidden;
      .swiper-container,.swiper-wrapper {
        height: 8.5rem;
      }
      .swiper-pagination-bullet-active{
        background: rgb(255, 146, 146);
      }
      .swiper-slide {
        height: 100%;
        >div{
          width: 100%;
          height: 100%;
          >img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .swiper-container {
        background-color: pink;
        margin-bottom: 0.55rem;
      }
      .store-state {
        height: 4rem;
        background: url('../assets/storebg.png') no-repeat 0 0;
        -webkit-background-size: 100% 100%;
        background-size: 100% 100%;
        margin-bottom: 0.6rem;
        color: white;
        display: flex;
      }
      .store-img {
        bottom: 0;
        width: 4.5rem;
        height: 4rem;
        box-sizing: border-box;
        padding: 0.25rem 0.5rem;
        background: url('https://g-search3.alicdn.com/img/bao/uploaded/i4/i4/94941669/TB2fquliCtkpuFjy0FhXXXQzFXa_!!94941669.jpg_230x230.jpg_.webp') no-repeat 0 0;
        -webkit-background-size: 100% 100%;
        background-size: 100% 100%;
        background-origin: content-box;
      }
      .store-name {
        padding-top: 0.8rem;
        flex: 1;
        >p{
          font-size: 0.8rem;
          line-height: 1.2rem;
        }
      }
    }
  }
</style>



