<template>
  <div id="personcenter" :class="{right: isRight}">
    <div id="wrap">
      <div class="head">
        <div class="icon">
          <img src="../assets/no4_face.png" alt="">
        </div>
        <div class="personname">追梦者<span v-show="isapproved">已认证</span><span v-show="!isapproved">未认证</span></div>
      </div>
      <div class="orders">
        <div class="line"></div>
        <div class="title">
          全部订单<router-link tag="span" to="/willpay">查看全部订单<span class="iconfont icon-youjiantou"></span></router-link>
        </div>
        <div class="line"></div>
        <div class="detail">
          <router-link tag="div" to="/willpay">
            <div class="top"><span class="iconfont icon-daifukuan"></span></div>
            <div class="bottom">待付款</div>
          </router-link>
          <div>
            <div class="top"><span class="iconfont icon-daifahuo"></span></div>
            <div class="bottom">待发货</div>
          </div>
          <div>
            <div class="top"><span class="iconfont icon-daishouhuo"></span></div>
            <div class="bottom">待收货</div>
          </div>
          <div>
            <div class="top"><span class="iconfont icon-yiwancheng"></span></div>
            <div class="bottom">已完成</div>
          </div>
          <div>
            <div class="top"><span class="iconfont icon-shouhou"></span></div>
            <div class="bottom">退款售后</div>
          </div>
        </div>
        <div class="line"></div>
      </div>
      <div class="otherlist">
        <div class="line"></div>
        <router-link tag="div" to="/addressedit" class="item">
          <div class="icon">
            <span class="iconfont icon-ccgl-dizhiliebiao-3"></span>
          </div>
          <div class="content">地址管理</div>
          <div class="next"><span class="iconfont icon-youjiantou"></span></div>
        </router-link>
        <div class="line"></div>
        <router-link tag="div" to="/approved" class="item">
          <div class="icon">
            <span class="iconfont icon-shenfenrenzheng"></span>
          </div>
          <div class="content">身份认证</div>
          <div class="next"><span class="iconfont icon-youjiantou"></span></div>
        </router-link>
        <div class="line"></div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript" >
  export default {
    created () {
    },
    computed: {
      isRight () {
        return this.$store.state.isRight
      },
      isapproved () {
        return this.$store.state.personInfo.approved
      }
    }
  }
</script>
<style lang="less">
  #personcenter{
    color: #333;
    overflow: hidden;
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    #wrap{
      height: 100%;
      background: #f4f4f4;
    }
    .head{
      height: 2.5rem;
      padding: 1rem;
      background: white;
      display: flex;
      margin-bottom: 0.5rem;
      >.icon{
        height: 2.5rem;
        width: 2.5rem;
        >img{
          width: 100%; 
          height: 100%;
          border-radius: 50%;
          vertical-align: middle;
        }
      }
      >.personname{
        flex: 1;
        line-height: 2.5rem;
        padding-left: 0.5rem;
        font-size: 0.9rem;
        >span{
          font-size: 0.7rem;
          float: right;
          color: #999;
        }
      }
    }
    .orders{
      margin-bottom: 0.5rem;
      background: white;
      >.title{
        padding: 0.5rem;
        font-size: 0.8rem;
        >span{
          float: right;
          font-size: 0.75rem;
          color: #999;
        }
      }
      >.detail{
        display: flex;
        height: 3rem;
        text-align: center;
        >div{
          width: 20%;
          color: #666;
          >.top{
            height: 1.4rem;
            padding-top: 0.3rem;
            >span{
              font-size: 1.1rem;
            }
          }
          >.bottom{
            font-size: 0.7rem;
          }
        }
      }
    }
    .otherlist{
      margin-bottom: 0.5rem;
      background: white;
      >.item{
        padding: 0.5rem;
        font-size: 0.8rem;
        height: 1.5rem;
        display: flex;
        >.icon{
          height: 1.5rem;
          width: 1.5rem;
          >.icon-ccgl-dizhiliebiao-3{
            font-size: 1.5rem;
            color: #eb5478;
            vertical-align: middle;
          }
          >.icon-shenfenrenzheng{
            font-size: 1.5rem;
            color: #f27c3c;
            vertical-align: middle;
          }
        }
        >.content{
          flex: 1;
          font-size: 0.8rem;
          padding-left: 0.5rem;
          line-height: 1.5rem;
        }
        >.next{
          line-height: 1.5rem;
          >span{
            color: #666;
          }
        }
      }
    }
  }
</style>



