<template>
  <div id="addaddress">
    <div id="wrap">
      <div class="line"></div>
      <ul>
        <li>
          <div class="content">
            <div class="left">收货人</div>
            <div class="right">
              <input type="text" placeholder="收货人姓名" v-model="address.pname">
            </div>
          </div>
          <div class="line"></div>
        </li>
        <li>
          <div class="content">
            <div class="left">手机号码</div>
            <div class="right">
              <input type="text" placeholder="11位手机号" v-model="address.phone">
            </div>
          </div>
          <div class="line"></div>
        </li>
        <li>
          <div class="content">
            <div class="left">选择省份</div>
            <div class="right">
              <el-select v-model="provinceValue" placeholder="所在省信息">
                <el-option
                  v-for="item in provinceOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="line"></div>
        </li>
        <li>
          <div class="content">
            <div class="left">选择城市</div>
            <div class="right">
              <el-select v-model="cityValue" placeholder="所在城市信息">
                <el-option
                  v-for="item in cityOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="line"></div>
        </li>
        <li>
          <div class="content">
            <div class="left">选择区/县</div>
            <div class="right">
              <el-select v-model="address.countyValue" placeholder="所在区县信息">
                <el-option
                  v-for="item in countyOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="line"></div>
        </li>
        <li>
          <div class="content">
            <div class="left">详细地址</div>
            <div class="right">
              <input type="text" placeholder="详细地址" v-model="address.addressinfo">
            </div>
          </div>
          <div class="line"></div>
        </li>
      </ul>
      <div class="line"></div>
    </div>
    <div class="save" @click="goBack()">
      <mt-button size="large" type="danger">保&nbsp;存</mt-button>
    </div>
  </div>
</template>
<script type="text/javascript" >
  export default {
    created () {
      if (this.$route.params.type === '0') {
        this.$store.dispatch('editAddress', {})
      } else {
        var editAddress = this.$store.state.editAddress
        this.provinceValue = editAddress.provinceID
        this.cityValue = editAddress.cityID
        this.address = {
          pname: editAddress.pname,
          phone: editAddress.phone,
          countyValue: editAddress.areaID,
          addressinfo: editAddress.addressinfo
        }
      }
      var provinceData = this.$store.state.allData.getProvince
      for (var i = 0; i < provinceData.length; i++) {
        this.provinceOptions.push({
          value: provinceData[i].provinceID,
          label: provinceData[i].province
        })
      }
    },
    data () {
      return {
        prePath: '',
        provinceOptions: [],
        cityOptions: [],
        countyOptions: [],
        provinceValue: '',
        cityValue: '',
        address: {
          panme: '',
          phone: '',
          countyValue: '',
          addressinfo: ''
        }
      }
    },
    methods: {
      goBack () {
        this.$router.go(-1)
      }
    },
    watch: {
      provinceValue (newProvinceValue) {
        this.cityOptions = []
        var cityData = this.$store.state.allData.getCityByFather
        for (var i = 0; i < cityData.length; i++) {
          this.cityOptions.push({
            value: cityData[i].cityID,
            label: cityData[i].city
          })
        }
      },
      cityValue (newCityValue) {
        this.countyData = []
        var countyData = this.$store.state.allData.getAreaByFather
        for (var i = 0; i < countyData.length; i++) {
          this.countyOptions.push({
            value: countyData[i].areaID,
            label: countyData[i].area
          })
        }
      }
    }
  }
</script>
<style lang="less">
  #addaddress{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    color: #333;
    background: #f4f4f4;
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    ul{
      padding-left: 0.5rem;
      background: #fff;
      >li{
        ::-webkit-input-placeholder { /* WebKit browsers */ 
          color: #999; 
        } 
        :-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
          color: #999; 
        } 
        ::-moz-placeholder { /* Mozilla Firefox 19+ */ 
          color: #999; 
        } 
        :-ms-input-placeholder { /* Internet Explorer 10+ */ 
          color: #999; 
        } 
        >.content{
          display: flex;
          font-size: 0.8rem;
          >.left{
            width: 4rem;
            line-height: 2.5rem;
          }
          >.right{
            flex: 1;
            >input{
              line-height: 2.5rem;
              border: none;
              font-size: 0.8rem;
              text-indent: 10px;
              &:focus{
                outline: none;
              }
            }
            .el-select{
              width: 100%;
              .el-input__inner{
                height: 2.5rem;
                font-size: 0.8rem;
                border: none;
              }
            }
          }
        }
      }
    }
    #wrap{
      flex: 1;
      margin-top: 0.5rem;
    }
    .save{
      padding: 0.5rem;
      background: #f4f4f4;
    }
    .mint-button--danger{
      font-size: 0.8rem;
      height: 2.5rem;
    }
  }
</style>



