<template>
  <div id="home">
    <div id="home_wrap">
      <transition name="pre_next">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>
    </div>
    <div id="footbar">
      <router-link tag="div" to="/" v-show="!isCommodity" id="home-page-btn">
        <div class="top"><span class="iconfont icon-shouye-shouye"></span></div>
        <div class="bottom">首页</div>
      </router-link>
      <div v-show="isCommodity" @click="goback()">
        <div class="top"><span class="iconfont icon-iconfontfanhui"></span></div>
        <div class="bottom">返回</div>
      </div>
      <router-link tag="div" to="/home/cart">
        <div class="top"><span class="iconfont icon-gouwuche"></span></div>
        <div class="bottom">购物车</div>
      </router-link>
      <router-link tag="div" to="/home/personcenter" v-show="!isCommodity">
        <div class="top"><span class="iconfont icon-geren"></span></div>
        <div class="bottom">个人中心</div>
      </router-link>
      <div class="addcart" v-show="isCommodity">
        加入购物车
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">

  export default {
    methods: {
      goback () {
        this.$router.go(-1)
      }
    },
    computed: {
      isCommodity () {
        if (this.$route.path !== '/home/commodity') {
          return false
        } else {
          return true
        }
      },
      nowPath () {
        return this.$route.path
      },
      isHome () {
        return this.$store.state.ishome
      }
    },
    mounted () {
      document.getElementById('home-page-btn').click()
    }
  }
</script>
<style lang="less">
  @import '../../static/css/animate.css';
  #home{
    height: 100%;
    display: flex;
    flex-direction: column;
    #home_wrap{
      flex: 1;
      width: 100%;
      overflow: hidden;
      position: relative;
      >div{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
    }
    .pre_next-enter-active{
      animation: slideInRight 0.5s;
    }
    .pre_next-leave-active{
      animation: slideOutLeft 0.5s;
    }
    .pre_next-enter-active.right{
      animation: slideInLeft 0.5s;
    }
    .pre_next-leave-active.right{
      animation: slideOutRight 0.5s;
    }
  }
  #footbar{
    height: 2.7rem;
    width: 100%;
    font-size: 0;
    border-top: 1px solid #cccccc;
    >div{
      width: 33.33%;
      height: 100%;
      font-size: 0.65rem;
      display: inline-block;
      text-align: center;
      padding-top: 0.2rem;
      box-sizing: border-box;
      color: #666666;
      .iconfont{
        font-size: 1.2rem;
      }
    }
    .router-link-exact-active{
      color: #ff4672;
    }
    >div.addcart{
      background: #e72755;
      color: white;
      line-height: 2.7rem;
      font-size: 0.8rem;
      padding-top: 0;
      vertical-align: top;
    }
  }
</style>



