<template>
  <div id="app">
    <home-page v-if="isHome"></home-page>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import HomePage from '@/components/HomePage'
import addData from '../static/data'
export default {
  name: 'app',
  created () {
    this.$store.dispatch('getAllData', addData)
  },
  components: {
    HomePage
  },
  computed: {
    isHome () {
      return this.$store.state.ishome
    }
  }
}
</script>

<style>
  html{
    font-size: 20px;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
  html, body, #app{
    height: 100%;
  }
  body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,
  form,fieldset,input,textarea,p,blockquote,th,td {
  padding: 0;
  margin: 0;
  }
  a{
      text-decoration: none;
  }
  table {
  border-collapse: collapse;
  border-spacing: 0;
  }
  fieldset,img {
  border: 0;
  }
  address,caption,cite,code,dfn,em,strong,th,var {
  font-weight: normal;
  font-style: normal;
  }
  ol,ul {
  list-style: none;
  }
  caption,th {
  text-align: left;
  }
  h1,h2,h3,h4,h5,h6 {
  font-weight: normal;
  font-size: 100%;
  }
  q:before,q:after {
  content:'';
  }
  abbr,acronym { border: 0;
  }
</style>



