<template>
  <div id="account">
    <div class="item_wrap address_wrap">
      <div class="line"></div>
      <router-link tag="div" to="/addressedit" class="address content">
        <div class="icon"><p><span class="iconfont icon-geren"></span></p></div>
        <div class="item">
          <p>收件人：</p>
          <p>收货地址：</p>
        </div>
        <div class="detail">
          <p>{{addressMoren.pname}}&nbsp;&nbsp;{{addressMoren.phone}}</p>
          <p>{{addressMoren.area.area}}{{addressMoren.area.city.city}}{{addressMoren.area.city.province.province}}{{addressMoren.addressinfo}}</p>
        </div>
        <div class="more"><span class="iconfont icon-youjiantou"></span></div>
      </router-link>
      <div class="line"></div>
    </div>
    <div class="item_wrap approve_wrap">
      <div class="line"></div>
      <router-link tag="div" to="/approved" class="approve content">
        <div class="detail">海关身份认证</div>
        <div class="more">{{ isapproved ? "已认证" : "未认证" }}&nbsp;<span class="iconfont icon-youjiantou"></span></div>
      </router-link>
      <div class="line"></div>
    </div>
    <div id="wrap">
      <div class="item_wrap products_wrap">
       <div class="line"></div>
       <div class="products content">
         <div class="head">
           <div class="right"><span>亲亲宝贝商城</span><div class="line"></div></div>
         </div>
         <div id="listes">
           <ul>
             <li v-for="(item, index) in cartGoods" @click="toDetail()">
                <div class="middle">
                  <img :src="item.goodList[0].goodPicPath" alt="">
                </div>
                <div class="right">
                  <p class="title">{{item.goodList[0].goodName}}</p>
                  <p class="size">
                    <span>颜色分类：灰蓝；</span>
                    <span>尺寸：中号</span>
                  </p>
                  <p class="price_count">
                    <span class="price">￥{{item.goodList[0].goodDiscountedPrice}}</span>
                    <span class="count">×&nbsp;{{item.goodList[0].cartNumber}}</span>
                  </p>
                </div>
             </li>
           </ul>
         </div>
         <div class="line"></div>
         <div class="delivery">
           配送方式<span>快递：包邮</span>
         </div>
         <div class="line"></div>
         <div class="msg">
           <div class="title">买家留言：</div>
           <div class="content">赠品要红色的</div>
         </div>
         <div class="line"></div>
         <div class="check">
           共2件商品&nbsp;&nbsp;合计：<span>￥{{totalPrice}}</span>
         </div>
       </div>
       <div class="line"></div>
     </div>
    </div>
    <div id="footer">
      <div class="line"></div>
      <div class="content">
        <div class="detail">实付款：<span>￥{{totalPrice}}元</span><span>（-809元）</span></div>
        <router-link to="/home/personcenter" class="pay">支付</router-link>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript" >
  import IScroll from '../../static/iscroll-lite.js'

  export default {
    created () {
      this.cartGoods = this.$store.state.allData.cartCon.selectCartOfUser
    },
    mounted () {
      setTimeout(() => {
        var myScroll = new IScroll('#listes', {
          click: true
        })
      }, 300)
    },
    data () {
      return {
        cartGoods: []
      }
    },
    methods: {
      toDetail () {
        let goodDetail = this.$store.state.allData.gc.getgood
        let goodImgs = this.$store.state.allData.gc.goodimage
        this.$store.dispatch('changeNowGood', { goodDetail, goodImgs })
        this.$router.push({ path: '/home/commodity' })
      }
    },
    computed: {
      isapproved () {
        return this.$store.state.personInfo.approved
      },
      totalPrice () {
        var total = 0
        for (var i = 0; i < this.cartGoods.length; i++) {
          total += this.cartGoods[i].goodList[0].goodDiscountedPrice
        }
        return total.toFixed(2)
      },
      addressMoren () {
        var qbuidData = this.$store.state.allData.qbuid
        for (var i = 0; i < qbuidData.length; i++) {
          if (qbuidData[i].status) {
            return qbuidData[i]
          }
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .line{
    height: 1px;
    background: #ccc;
    transform: scaleY(0.5);
  }
  #account{
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #f4f4f4;
  }
  #wrap{
    flex: 1;
    overflow: hidden;
  }
  .item_wrap{
    margin-top: 0.5rem;
    &.products_wrap{
      display: flex;
      flex-direction: column;
      height: 100%;
      overflow: hidden;
    }
    >.content{
      background: white;
      padding: 0.5rem;
    }
  }
  .approve{
    display: flex;
    font-size: 0.8rem;
    >.detail{
      flex: 1;
      line-height: 2.5rem;
    }
    >.more{
      color: #666;
      text-align: right;
      line-height: 2.5rem;
    }
  }
  .address{
    height: 4rem;
    display: flex;
    font-size: 0.8rem;
    color: #333;
    >div>p{
        line-height: 1.33rem;
      }
    >.icon{
      width: 1.1rem;
      .iconfont{
        color: #e72755;
        font-size: 0.9rem;
      }
    }
    >.item{
      width: 4rem;
    }
    >.detail{
      flex: 1;
      >p:last-child{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
    }
    >.more{
      width: 1rem;
      text-align: right;
      line-height: 4rem;
    }
  }
  .item_wrap > .products{
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 0 0.5rem 0.5rem;
  }
  .delivery{
    padding: 0.5rem 0;
    font-size: 0.8rem;
    >span{
      font-size: 0.7rem;
      float: right;
    }
  }
  .products{
    background: white;
    color: #333;  
    >.head{
      height: 2rem;
      display: flex;
      >.right{
        flex: 1;
        line-height: 2rem;
        font-size: 0.9rem;
      }
    }
    >#listes{
      flex: 1;
      overflow: hidden;
      >ul{
        >li{
          height: 3rem;
          display: flex;
          padding: 0.5rem 0 0.5rem 0.2rem;
          >.middle{
            width: 3rem;
            height: 3rem;
            box-sizing: border-box;
            border: 1px solid #ccc;
            border-radius: 0.25rem;
            >img{
              width: 100%;
              height: 100%;
              border-radius: 0.25rem;
            }
          }
          >.right{
            flex: 1;
            color: #333;
            line-height: 3rem;
            font-size: 0.75rem;
            margin-left: 0.4rem;
            >.title{
              color: #333;
              line-height: 1rem;
              font-size: 0.75rem;
            }
            >.size{
              color: #999;
              font-size: 0.6rem;
              line-height: 1.1rem;
            }
            >.price_count{
              line-height: 1rem;
            }
            .price{
              color: #e72755;
              font-size: 0.75rem;
            }
            .count{
              color: #999;
              font-size: 0.6rem;
              float: right;
            }
          }
        }
      }
    }
    >.msg{
      display: flex;
      padding: 0.5rem 0;
      font-size: 0.8rem;
      >.title{
        width: 4rem;
        line-height: 1.1rem;
        color: #999;
      }
      >.content{
        flex: 1;
        line-height: 1.1rem;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
      }
    }
    >.check{
      padding: 0.5rem 0;
      text-align: right;
      font-size: 0.8rem;
      >span{
        color: #e72755;
      }
    }
  }
  #footer{
    background: white;
    margin-top: 1rem;
    >.content{
      height: 2.5rem;
      display: flex;
      font-size: 0.8rem;
      >.pay{
        width: 4.5rem;
        line-height: 2.5rem;
        background: #e72755;
        color: white;
        text-align: center;
      }
      >.detail{
        line-height: 2.5rem;
        text-align: center;
        flex: 1;
        >span{
          &:first-child{
            /*font-weight: 600;*/
          }
          &:last-child{
            font-size: 0.7rem;
            color: #666;
          }
        }
      }
    }
  }
</style>



