<template>
  <div id="wrap-box">
    <div v-for="(item, index) in allGood">
      <div class="wrap-title" style="font-weight: bold;">{{item.goodsStyle}}&nbsp;<img src="../assets/wraphot.png"></div>
      <div class="wrap-ctn">
        <ul>
          <li v-for="(ele, number) in item.goods" class="commodity-state" @click="linkTo(ele)">
            <div class="commodity-box">
              <div class="commodity-img"><img :src="ele.goodPicPath"></div>
              <div><span class="commodity-description"><i v-show="!baoyou(ele)" class="commodity-postage">【包邮】</i>{{ele.goodDesc}}</span>
              </div>
              <div>
                <span class="commodity-offprice">￥{{ele.goodDiscountedPrice}}<sub class="commodity-price">￥{{ele.goodOriginalPrice}}</sub></span>
                <i class="cart-icon"><img src="../assets/carticon.png"></i>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">

  export default {
    name: 'wrap-box',
    data () {
      return {
        getgoodtype: [],
        allGood: []
      }
    },
    created () {
      this.getgoodtype = this.$store.state.allData.gc.getgoodtype
      this.getgoodtype.forEach(item => {
        this.allGood.push({
          goodsStyle: item.styleName,
          goods: this.$store.state.allData.gc.getallgood
        })
      })
    },
    methods: {
      baoyou (ele) {
        return ele.serviceType.some(item => {
          item === '超值包邮'
        })
      },
      linkTo (ele) {
        let goodDetail = this.$store.state.allData.gc.getgood
        let goodImgs = this.$store.state.allData.gc.goodimage
        this.$store.dispatch('changeNowGood', { goodDetail, goodImgs })
        this.$router.push({ path: '/home/commodity' })
      }
    }
  }
</script>

<style lang="less">
	#wrap-box{
    >div{
      width: 100%;
      height: 100%;
    }
		.wrap-title {
	    margin: 0.4rem 0;
	    font-size: 0.9rem;
	    text-align: center;
	    vertical-align: middle;
	  }
	  .wrap-title img {
	    width: 2%;
	  }
	  .wrap-ctn ul {
	    display: flex;
	    -webkit-justify-content: space-around;
	    justify-content: space-around;
	    -webkit-flex-wrap: wrap;
	    flex-wrap: wrap;
	    width: 100%;
	    list-style: none;
	  }
	  .commodity-state {
	    position: relative;
	    width: 48%;
	    border: 1px solid #d0d0d0;
	    box-sizing: border-box;
	    margin-bottom: 0.4rem;
	  }
	  .commodity-box {
	    position: relative;
	    width: 89.67%;
	    margin: 0.5rem 0.5rem 0 0.5rem;
	  }
    .commodity-img:before{
      content:"";
      display: inline-block;
      padding-bottom: 100%;
      width: 0px;
      vertical-align: top;
    }
	  .commodity-img img {
	    width: 100%;
      height: 100%;
	    border-radius: 0.5rem;
      vertical-align: top;
	  }
	  .commodity-description {
	    display: block;
	    margin: 0.538rem 0;
	    color: #333;
	    font-size: 0.75rem;
	    text-align: left;
	    display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
	  }
	  .commodity-postage {
	    font-style: normal;
	    color: #E72B58;
	  }
	  .commodity-offprice {
	    left: 0;bottom: 0;
	    color: #E72B58;
	    font-size: 0.9rem;
	    font-weight: 600;
	  }
	  .commodity-price {
	    margin-left: 2.25%;
	    color: #999;
	    font-size: 0.7rem;
	    text-decoration: line-through;
	    vertical-align: middle;
	  }
	  .cart-icon {
	    width: 1.5rem;
	    display: inline-block;
	    right: 0;
	    bottom: 0;
	    vertical-align: middle;
	  }
	  .cart-icon img {
	    width: 100%;
	  }
	}
</style>



