<template>
  <div id="cart" :class="{right: isRight}">
    <div id="wrap">
    <div class="nogoods" v-show="!cartGoods.length">购物车里暂时还没有商品哦...</div>
     <div v-show="cartGoods.length" class="item_wrap">
       <div class="line"></div>
       <div class="content">
         <div class="head">
           <div class="left" @click="chooseAll()">
            <span class="select-wrap" :class="{active:allSelected}">
              <span class="iconfont icon-6gou" :class="{active:allSelected}"></span>
            </span>
           </div>
           <div class="right"><span>亲亲宝贝商城</span><span class="edit" v-show="onEdit" @click="onEdit=!onEdit">完成</span><span class="edit" v-show="!onEdit" @click="onEdit=!onEdit">编辑</span><div class="line"></div></div>
         </div>
         <ul class="listes">
           <li v-for="(item, index) in cartGoods">
              <div class="left" @click="isAll(item)">
                <span class="select-wrap" :class="{active:item.selected}">
                  <span class="iconfont icon-6gou" :class="{active:item.selected}"></span>
                </span>
              </div>
              <div class="middle" @click="toDetail(item)">
                <img :src="item.goodList[0].goodPicPath" alt="">
              </div>
              <div class="right">
                <p class="title">{{item.goodList[0].goodName}}</p>
                <p class="size">
                  <span>颜色分类：灰蓝；</span>
                  <span>尺寸：中号</span>
                </p>
                <p class="price_count">
                  <span class="price">￥{{item.goodList[0].goodDiscountedPrice.toFixed(2)}}</span>
                  <span class="count">×&nbsp;{{item.goodList[0].cartNumber}}</span>
                </p>
                <div class="change_count"  v-show="onEdit">
                  <div class="change sub" @click="sub(item)">-</div>
                  <div class="show">
                    <input type="text" v-model="item.goodList[0].cartNumber" @change="changeCount(item)">
                  </div>
                  <div class="change" @click="add(item)">+</div>
                </div>
              </div>
           </li>
         </ul>
       </div>
       <div class="line"></div>
     </div>
    </div>
    <div  v-show="cartGoods.length" class="check">
      <div class="line"></div>
      <div class="content">
        <div class="left" @click="chooseAll()">
          <span class="select-wrap" :class="{active:allSelected}">
            <span class="iconfont icon-6gou" :class="{active:allSelected}"></span>
          </span>
        </div>
        <div class="all">全选</div>
        <div class="middle">
          <p class="infact">合计：￥{{totalPrice.toFixed(2)}}</p>
          <p class="detail">总额：￥{{totalPrice.toFixed(2)}}&nbsp;&nbsp;立减：￥50.00</p>
        </div>
        <router-link to="/account" class="right">结算</router-link>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript" >
  export default {
    created () {
      this.cartGoods = this.$store.state.allData.cartCon.selectCartOfUser
      for (let i = 0; i < this.cartGoods.length; i++) {
        this.cartGoods[i].selected = false
      }
    },
    data () {
      return {
        allSelected: false,
        onEdit: false,
        cartGoods: [],
        totalPrice: 0
      }
    },
    components: {
    },
    computed: {
      isRight () {
        return this.$store.state.isRight
      }
    },
    methods: {
      toDetail (item) {
        let goodDetail = this.$store.state.allData.gc.getgood
        let goodImgs = this.$store.state.allData.gc.goodimage
        this.$store.dispatch('changeNowGood', { goodDetail, goodImgs })
        this.$router.push({ path: '/home/commodity' })
      },
      add (item) {
        item.goodList[0].cartNumber += 1
        this.changetotalPrice()
      },
      sub (item) {
        if (item.goodList[0].cartNumber > 1) {
          item.goodList[0].cartNumber -= 1
          this.changetotalPrice()
        }
      },
      chooseAll () {
        this.allSelected = !this.allSelected
        for (var i = 0; i < this.cartGoods.length; i++) {
          if (this.allSelected) {
            this.cartGoods[i].selected = true
          } else {
            this.cartGoods[i].selected = false
          }
        }
        this.changetotalPrice()
      },
      changeCount (item) {
        if (isFinite(Number(item.goodList[0].cartNumber))) {
          if (Number(item.goodList[0].cartNumber) < 1) {
            item.goodList[0].cartNumber = 1
          }
        } else {
          item.goodList[0].cartNumber = 1
        }
        this.changetotalPrice()
      },
      isAll (ele) {
        ele.selected = !ele.selected
        var bol = this.cartGoods.every(item => item.selected)
        if (bol) {
          this.allSelected = true
        } else {
          this.allSelected = false
        }
        this.changetotalPrice()
      },
      changetotalPrice () {
        var total = 0
        for (var i = 0; i < this.cartGoods.length; i++) {
          if (this.cartGoods[i].selected) {
            total += this.cartGoods[i].goodList[0].cartNumber * this.cartGoods[i].goodList[0].goodDiscountedPrice
          }
        }
        this.totalPrice = total
      }
    }
  }
</script>
<style lang="less">
  #cart{
    overflow: hidden;
    display: flex;
    flex-direction: column;
    #wrap{
      overflow: scroll;
      flex: 1;
      background: #f4f4f4;
      color: #333;
      >.nogoods{
        background: #fff;
        text-align: center;
        margin-top: 0.5rem;
        color: #999;
        height: 5rem;
        line-height: 5rem;
        font-size: 0.9rem;
      }
    }
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    .mint-cell:last-child{
      background-image: none;
    }
    .mint-cell-wrapper{
      background-image: none;
    }
    .mint-checkbox-input:checked+.mint-checkbox-core {
      background-color: #e72755;
      border-color: #e72755;
    }
    .check{
      height: 2.7rem;
      >.content{
        padding-left: 0.5rem;
        display: flex;
        font-size: 0.9rem;
        >.left{
          width: 1.5rem;
          line-height: 2.7rem;
          >.select-wrap{
            display: inline-block;
            width: 1rem;
            height: 1rem;
            border: 1px solid #ddd;
            border-radius: 50%;
            transition: all 0.2s;
            vertical-align: middle;
            &.active{
              border-color: #e72755;
              background-color: #e72755;
            }
            >.iconfont{
              opacity: 0;
              font-size: 1rem;
              color: #fff;
              transition: all 0.2s;
              position: relative;
              top: -0.75rem;
              &.active{
                opacity: 1;
              }
            }
          }
        }
        >.all{
          line-height: 2.7rem;
        }
        >.middle{
          flex: 1;
          padding-left: 1rem;
          >.infact{
            line-height: 1.5rem;
          }
          >.detail{
            line-height: 1rem;
            font-size: 0.6rem;
            color: #333;
          }
        }
        >.right{
          width: 4.5rem;
          line-height: 2.7rem;
          background: #e72755;
          color: white;
          text-align: center;
        }
      }
    }
    .item_wrap{
      padding-top: 0.45rem;
      >.content{
        padding: 0 0.5rem;
        background: white;
        >.head{
          height: 2rem;
          display: flex;
          >.left{
            width: 1.5rem;
            line-height: 2rem;
            >.select-wrap{
              display: inline-block;
              width: 1rem;
              height: 1rem;
              border: 1px solid #ddd;
              border-radius: 50%;
              transition: all 0.2s;
              vertical-align: middle;
              &.active{
                border-color: #e72755;
                background-color: #e72755;
              }
              >.iconfont{
                opacity: 0;
                font-size: 1rem;
                color: #fff;
                transition: all 0.2s;
                position: relative;
                top: -0.4rem;
                &.active{
                  opacity: 1;
                }
              }
            }
          }
          >.right{
            flex: 1;
            color: #333;
            line-height: 2rem;
            font-size: 0.75rem;
            .edit{
              float: right;
            }
          }
        }
        >.listes>li{
          height: 3rem;
          display: flex;
          padding: 0.5rem 0 0.5rem 0.2rem;
          >.left{
            width: 1.5rem;
            line-height: 3rem;
            >.select-wrap{
              display: inline-block;
              width: 1rem;
              height: 1rem;
              border: 1px solid #ddd;
              border-radius: 50%;
              transition: all 0.2s;
              vertical-align: middle;
              &.active{
                border-color: #e72755;
                background-color: #e72755;
              }
              >.iconfont{
                opacity: 0;
                font-size: 1rem;
                color: #fff;
                transition: all 0.2s;
                position: relative;
                top: -1rem;
                &.active{
                  opacity: 1;
                }
              }
            }
          }
          >.middle{
            width: 3rem;
            height: 3rem;
            box-sizing: border-box;
            border: 1px solid #ccc;
            border-radius: 0.25rem;
            >img{
              width: 100%;
              height: 100%;
              border-radius: 0.25rem;
            }
          }
          >.right{
            flex: 1;
            color: #333;
            line-height: 3rem;
            font-size: 0.75rem;
            margin-left: 0.4rem;
            position: relative;
            >.title{
              color: #333;
              line-height: 1rem;
              font-size: 0.75rem;
            }
            >.size{
              color: #999;
              font-size: 0.6rem;
              line-height: 1.1rem;
            }
            >.price_count{
              line-height: 1rem;
            }
            .price{
              color: #e72755;
              font-size: 0.75rem;
            }
            .count{
              color: #999;
              font-size: 0.6rem;
              float: right;
            }
            >.change_count{
              width: 3.2rem;
              height: 1rem;
              position: absolute;
              right: 0rem;
              top: 0.5rem;
              display: flex;
              border: 1px solid rgba(231, 39, 85, 0.3);
              border-radius: 3px;
              >.change{
                width: 1rem;
                line-height: 1rem;
                text-align: center;
                background: rgba(231, 39, 85, 0.1);
                &.sub{
                  font-size: 1rem;
                }
              }
              >.show{
                flex: 1;
                border: solid rgba(231, 39, 85, 0.3);
                border-width: 0 1px;
                position: relative;
                >input{
                  width: 100%;
                  height: 100%;
                  border: none;
                  margin: 0;
                  padding: 0;
                  text-align: center;
                  position: absolute;
                  top: 0;
                  left: 0;
                  box-sizing: border-box;
                  padding-top: 3px;
                  &:focus{
                    outline: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



