<template>
  <div id="willpay">
    <div v-show="!orders.length" class="noorder">暂时还没有未付款的订单哦...
      <br><router-link to="/">点击此处返回首页</router-link>
    </div>
    <div v-show="orders.length" class="lefttime">
      <div class="left">等待买家付款</div>
      <div class="right">
        剩余&nbsp;<span>2</span>&nbsp;天&nbsp;<span>21</span>&nbsp;时&nbsp;<span>21</span>&nbsp;分
      </div>
    </div>
    <div v-show="orders.length" class="item_wrap address_wrap">
      <div class="line"></div>
      <router-link tag="div" to="/addressedit" class="address content">
        <div class="icon"><p><span class="iconfont icon-geren"></span></p></div>
        <div class="item">
          <p>收件人：</p>
          <p>收货地址：</p>
        </div>
        <div class="detail">
          <p>{{addressMoren.pname}}&nbsp;&nbsp;{{addressMoren.phone}}</p>
          <p>{{addressMoren.area.area}}{{addressMoren.area.city.city}}{{addressMoren.area.city.province.province}}{{addressMoren.addressinfo}}</p>
        </div>
        <div class="more"><span class="iconfont icon-youjiantou"></span></div>
      </router-link>
      <div class="line"></div>
    </div>
    <div v-show="orders.length" id="wrap">
      <div class="line"></div>
      <div id="scroll_wrap">
        <div>
          <div v-for="(item, index) in orders" class="item_wrap products_wrap">
            <div class="line" v-show="index!==0"></div>
            <div class="approve content" @click="isAll(item)">
              <div class="detail">
                <div class="checkbutton">
                  <span class="select-wrap" :class="{active:item.selected}">
                    <span class="iconfont icon-6gou" :class="{active:item.selected}"></span>
                  </span>
                </div>
                <div class="order">
                  <span>订单编号：{{orderId(item.orderid)}}</span>
                </div>
              </div>
            </div>
            <div class="line"></div>
            <div class="products content">
              <div class="head">
                <div class="right"><span>亲亲宝贝商城</span><div class="line"></div></div>
              </div>
              <ul class="listes">
                <li v-for="(ele, order) in item.orderItem"  @click="toDetail(ele)">
                  <div class="middle">
                    <img :src="ele.goodInfo.goodPicPath" alt="">
                  </div>
                  <div class="right">
                    <p class="title">{{ele.goodInfo.goodName}}</p>
                    <p class="size">
                      <span>套装；</span>
                      <span>开本：32开</span>
                    </p>
                    <p class="price_count">
                      <span class="price">￥{{ele.goodInfo.goodDiscountedPrice}}</span>
                      <span class="count">×{{ele.quantity}}</span>
                    </p>
                  </div>
               </li>
             </ul>
             <div class="line"></div>
             <div class="delivery">
               综合税<span>{{shuishou(item)}}元</span>
             </div>
             <div class="line"></div>
             <div class="delivery">
               配送方式<span>快递：包邮</span>
             </div>
             <div class="line"></div>
             <div class="msg">
               <div class="title">买家留言：</div>
               <div class="content">{{item.message}}</div>
             </div>
             <div class="line"></div>
             <div class="check">
              <p>下单时间：{{item.ordertime}}</p>
              <p>共2件商品&nbsp;&nbsp;合计：￥{{goodsPrice(item)}}</p>
              <p class="pay">实付款：<span>￥{{(goodsPrice(item)+shuishou(item)).toFixed(2)}}元</span></p>
             </div>
             <div class="line"></div>
             <div class="footer products_footer">
              <div class="submit_btn">
                <mt-button size="small" type="default" @click="deleteThisOrder(item)">取消订单</mt-button>
                <mt-button size="small" type="danger" @click="payItem(item)">去支付</mt-button>
              </div>
             </div>
           </div>
           <div class="line" v-show="index!==orders.length-1"></div>
         </div>
        </div>
      </div>
      <div class="line"></div>
    </div>
    <div v-show="orders.length" class="footer">
      <div class="all_btn" @click="chooseAll()">
        <span class="select-wrap" :class="{active:allSelected}">
          <span class="iconfont icon-6gou" :class="{active:allSelected}"></span>
        </span>
        全选
      </div>
      <div class="submit_btn">
        <mt-button size="small" type="default" @click="deleteOrders()">取消订单</mt-button>
        <mt-button size="small" type="danger" @click="payAll()">去支付</mt-button>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript" >
  import IScroll from '../../static/iscroll-lite.js'
  
  export default {
    created () {
      this.orders = this.$store.state.allData.order.showorderinfo
      for (let i = 0; i < this.orders.length; i++) {
        this.orders[i].selected = false
      }
      var qbuidData = this.$store.state.allData.qbuid
      for (var i = 0; i < qbuidData.length; i++) {
        if (qbuidData[i].status) {
          this.addressMoren = qbuidData[i]
          break
        }
      }
    },
    mounted () {
      setTimeout(() => {
        var myScroll = new IScroll('#scroll_wrap', {
          click: true
        })
      }, 300)
    },
    data () {
      return {
        allSelected: false,
        orders: [],
        addressMoren: {}
      }
    },
    methods: {
      orderId (orderid) {
        var length = orderid.toString().length
        var zero = '0'
        return zero.repeat(11 - length) + orderid
      },
      shuishou (item) {
        var num = 0
        var ele = item.orderItem
        for (var i = 0; i < ele.length; i++) {
          num += (ele[i].goodInfo.goodOriginalPrice - ele[i].goodInfo.goodDiscountedPrice) * 0.2
        }
        return parseFloat(num.toFixed(2))
      },
      goodsPrice (item) {
        var num = 0
        var ele = item.orderItem
        for (var i = 0; i < ele.length; i++) {
          num += ele[i].goodInfo.goodDiscountedPrice
        }
        return parseFloat(num.toFixed(2))
      },
      toDetail (ele) {
        let goodDetail = this.$store.state.allData.gc.getgood
        let goodImgs = this.$store.state.allData.gc.goodimage
        this.$store.dispatch('changeNowGood', { goodDetail, goodImgs })
        this.$router.push({ path: '/home/commodity' })
      },
      isAll (ele) {
        ele.selected = !ele.selected
        var bol = this.orders.every(item => item.selected)
        if (bol) {
          this.allSelected = true
        } else {
          this.allSelected = false
        }
      },
      chooseAll () {
        this.allSelected = !this.allSelected
        for (let y = 0; y < this.orders.length; y++) {
          if (this.allSelected) {
            this.orders[y].selected = true
          } else {
            this.orders[y].selected = false
          }
        }
      },
      payItem (item) {
        console.log('item')
        this.$router.push('/home/personcenter')
      },
      payAll () {
        console.log('====')
        this.$router.push('/home/personcenter')
      },
      deleteThisOrder (item) {
        this.orders.splice(this.orders.indexOf(item), 1)
      },
      deleteOrders () {
        this.orders = []
      },
      changetotalPrice () {

      }
    }
  }
</script>
<style lang="less">
  #willpay{
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #f4f4f4;
    >.noorder{
      background: #fff;
      text-align: center;
      margin-top: 0.5rem;
      color: #999;
      height: 5rem;
      line-height: 1.5rem;
      box-sizing: border-box;
      padding-top: 1rem;
      font-size: 0.9rem;
      >a{
        color: rgba(0, 0, 255, .6);
        text-decoration: underline;
      }
    }
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    .mint-cell:last-child{
      background-image: none;
    }
    .mint-cell-wrapper{
      background-image: none;
    }
    .mint-checkbox-input:checked+.mint-checkbox-core {
      background-color: #e72755;
      border-color: #e72755;
    }
    .lefttime{
      height: 1rem;
      display: flex;
      padding: 0.5rem;
      background: #e72755;
      color: white;
      line-height: 1rem;
      font-size: 0.8rem;
      >.left{
        flex: 1;
      }
      >.right{
        text-align: right;
        >span{
          display: inline-block;
          width: 1rem;
          height: 1rem;
          background: white;
          color: #e72755;
          text-align: center;
          line-height: 1.2rem;
        }
      }
    }
    .item_wrap{
      margin-top: 0.5rem;
      &.products_wrap{
        margin-top: 0rem;
        margin-bottom: 0.5rem;
      }
      >.content{
        background: white;
        padding: 0.5rem;
        &.products{
          padding: 0 0.5rem;
        }
      }
    }
    .address{
      height: 4rem;
      display: flex;
      font-size: 0.8rem;
      color: #333;
      >div>p{
          line-height: 1.33rem;
        }
      >.icon{
        width: 1.1rem;
        .iconfont{
          color: #e72755;
          font-size: 0.9rem;
        }
      }
      >.item{
        width: 4rem;
      }
      >.detail{
        flex: 1;
      }
      >.more{
        width: 1rem;
        text-align: right;
        line-height: 4rem;
      }
    }
    .approve{
      font-size: 0.8rem;
      color: #999;
      >.detail{
        height: 1.2rem;
        display: flex;
        >.checkbutton{
          width: 1.3rem;
          height: 100%;
          >.select-wrap{
            display: inline-block;
            width: 1rem;
            height: 1rem;
            border: 1px solid #ddd;
            border-radius: 50%;
            transition: all 0.2s;
            &.active{
              border-color: #e72755;
              background-color: #e72755;
            }
            >.iconfont{
              opacity: 0;
              font-size: 1rem;
              color: #fff;
              transition: all 0.2s;
              position: relative;
              top: 0rem;
              &.active{
                opacity: 1;
              }
            }
          }
        }
        >.order{
          flex: 1;
          line-height: 1.2rem;
        }
      }
    }
    #wrap{
      flex: 1;
      margin-top: 0.5rem;
      overflow: hidden;
      display: flex;
      flex-direction: column;
    }
    #scroll_wrap{
      flex: 1;
      overflow: hidden;
      background: #f4f4f4;
    }
    .delivery{
      padding: 0.5rem 0;
      font-size: 0.8rem;
      >span{
        font-size: 0.7rem;
        float: right;
      }
    }
    .products{
      background: white;
      color: #333;  
      >.head{
        height: 2rem;
        display: flex;
        >.right{
          flex: 1;
          line-height: 2rem;
          font-size: 0.9rem;
        }
      }
      >.listes{
        max-height: 8rem;
        overflow: scroll;
        >li{
          height: 3rem;
          display: flex;
          padding: 0.5rem 0 0.5rem 0.2rem;
          >.middle{
            width: 3rem;
            height: 3rem;
            box-sizing: border-box;
            border: 1px solid #ccc;
            border-radius: 0.25rem;
            >img{
              width: 100%;
              height: 100%;
              border-radius: 0.25rem;
            }
          }
          >.right{
            flex: 1;
            color: #333;
            line-height: 3rem;
            font-size: 0.75rem;
            margin-left: 0.4rem;
            >.title{
              color: #333;
              line-height: 1rem;
              font-size: 0.75rem;
            }
            >.size{
              color: #999;
              font-size: 0.6rem;
              line-height: 1.1rem;
            }
            >.price_count{
              line-height: 1rem;
            }
            .price{
              color: #e72755;
              font-size: 0.75rem;
            }
            .count{
              color: #999;
              font-size: 0.6rem;
              float: right;
            }
          }
        }
      }
      >.msg{
        display: flex;
        padding: 0.5rem 0;
        font-size: 0.8rem;
        >.title{
          width: 4rem;
          line-height: 1.1rem;
          color: #999;
        }
        >.content{
          flex: 1;
          line-height: 1.1rem;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
      >.check{
        padding: 0.25rem 0;
        >p{
          line-height: 1.2rem;
          font-size: 0.8rem;
          color: #666;
          text-align: right;
          &.pay{
            font-size: 0.9rem;
            color: #333;
            >span{
              font-weight: bold;
            }
          }
        }
      }
    }
    .footer{
      height: 2.5rem;
      line-height: 2.3rem;
      border-top: 1px solid #ccc;
      background: white;
      margin-top: 1rem;
      padding: 0 0.5rem;
      display: flex;
      >.all_btn{
        flex: 1;
        >.select-wrap{
          display: inline-block;
          width: 1rem;
          height: 1rem;
          border: 1px solid #ddd;
          border-radius: 50%;
          transition: all 0.2s;
          vertical-align: middle;
          position: relative;
          &.active{
            border-color: #e72755;
            background-color: #e72755;
          }
          >.iconfont{
            opacity: 0;
            font-size: 1rem;
            color: #fff;
            transition: all 0.2s;
            vertical-align: top;
            position: relative;
            top: -0.6rem;
            &.active{
              opacity: 1;
            }
          }
        }
      }
      >.submit_btn{
        height: 2.5rem;
        text-align: right;
        .mint-button--danger.mint-button::after{
          background-color: #e72755;
        }
      }
      &.products_footer{
        border-top: none;
        margin-top: 0;
        padding: 0;
        >.submit_btn{
          width: 100%;
        }
      }
      .mint-button--small{
        width: 4.2rem;
        box-shadow: 0 0 1px #b8bbbf;
        background-color: #fff;
      }
      .mint-button--danger {
        color: #e72755;
        border: 1px solid #e72755;
      }
      .mint-button--default {
        color: #333;
        border: 1px solid #333;
      }
    }
  }
</style>



