<template>
  <div id="approved">
    <div id="wrap">
      <div class="tip">请填写你的身份证上的真实信息，以用于报关审核</div>
      <div class="item">
        <div class="line"></div>
        <div class="content_wrap">
          <div class="isapproved" v-if="!isapproved">
            <div class="list">
              <div class="left">真实姓名</div>
              <div class="right">
                <input type="text" v-model="userName" placeholder="请在这里填写">
              </div>
            </div>
            <div class="line"></div>
            <div class="list">
              <div class="left">身份证号</div>
              <div class="right">
                <input type="text" v-model="userId" placeholder="请在这里填写">
              </div>
            </div>
            <div class="line"></div>
          </div>
          <div class="isapproved" v-else>
            <div class="list hasapproved">
              已认证
            </div>
            <div class="line"></div>
          </div>
          <div class="information">
            <p>1. 由于海外沟等直邮商品需办理入境申报，请你配合进行实名认证。</p>
            <p>2. 亲亲宝贝承诺所传身份证用于购买直邮商品的清关手续，不做其他用途使用，其他任何人均无法查看。</p>
            <p>3. 由于身份认证信息不符导致的物流延缓由用户自己负责。</p>
          </div>
        </div>
        <div class="line"></div>
      </div>
      <div class="warning" :class="{out: isOut}">请按正确的格式填写</div>
    </div>
    <div class="finish" @click="goBack()">
      <mt-button size="large" type="danger">完&nbsp;成</mt-button>
    </div>
  </div>
</template>
<script type="text/javascript" >
    export default {
      data () {
        return {
          userName: '',
          userId: '',
          isOut: false
        }
      },
      computed: {
        isapproved () {
          return this.$store.state.personInfo.approved
        }
      },
      methods: {
        goBack () {
          if (!this.isapproved) {
            if (this.userName && /\d+/.test(this.userId)) {
              this.$store.state.personInfo.approved = true
              this.$router.go(-1)
            } else {
              this.isOut = true
              setTimeout(() => {
                this.isOut = false
              }, 3000)
            }
          } else {
            this.$router.go(-1)
          }
        }
      }
    }
</script>
<style lang="less">
  #approved{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    color: #333;
    .line{
      height: 1px;
      background: #ccc;
      transform: scaleY(0.5);
    }
    #wrap{
      flex: 1;
      background: #f4f4f4;
      >.warning{
        width: 50%;
        background-color: #efefef;
        line-height: 5rem;
        padding: 0 2rem;
        border-radius: 0.5rem;
        border: 1px solid #ddd;
        margin: 2rem auto;
        opacity: 0;
        transition: all .5s ease;
      }
      >.warning.out{
        opacity: 1;
      }
    }
    .item{
      background: white;
      >.content_wrap{
        padding: 0 0.5rem;
        >.isapproved{
          >.list{
            display: flex;
            font-size: 0.8rem;
            height: 2.5rem;
            >.left{
              width: 4.5rem;
              line-height: 2.5rem;
              text-indent: 0.4rem;
            }
            >.right{
              flex: 1;
              >input{
                line-height: 2.5rem;
                font-size: 0.8rem;
                border: none;
                &:focus{
                  outline: none;
                }
              }
            }
          }
          >.hasapproved{
            display: block;
            text-align: center;
            line-height: 2.5rem;
            font-size: 0.9rem;
          }
        }
        >.information{
          padding: 0.7rem 0 0.5rem;
          >p{
            line-height: 1rem;
            font-size: 0.7rem;
            color: #666;
            margin-bottom: 0.15rem;
          }
        }
      }
    }
    .tip{
      padding: 0.5rem;
      font-size: 0.7rem;
      color: #e72755;
    }
    .finish{
      padding: 0.5rem;
      background: #f4f4f4;
    }
    .mint-button--danger{
      font-size: 0.8rem;
      height: 2.5rem;
    }
  }
</style>



