<template>
  <div id="commodity" :class="{right: isRight}">
  	<div id="wrap">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(item, index) in nowGood.goodImgs">
          <img :src="item.picPath" alt="">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
	    <ul>
	      <li class="commodity-header">
	        <span class="commodity-name">{{nowGood.goodDetail.goodName}}</span>
	        <span class="commodity-sale"><sub>￥</sub>{{nowGood.goodDetail.goodDiscountedPrice}}
          <sub class="originalprice">原价：<sub>￥</sub>{{nowGood.goodDetail.goodOriginalPrice}}</sub>
          <i class="commodity-posted" v-show="baoyouBol">包邮</i></span>
	      </li>
	      <li class="commodity-support">
	        <ul>
	          <li v-show="baoyouBol"><span style="font-size: 0.9rem;" class="iconfont icon-zhengpinbaozheng"></span>&nbsp;正品包邮</li>
	          <li v-show="shantuiBol"><span style="font-size: 1.2rem;" class="iconfont icon-shandianjiaoyi"></span>&nbsp;闪电退款</li>
	          <li v-show="baotuiBol"><span style=" font-size: 0.7rem;" class="iconfont icon-7tianwuliyoutuihuo"></span>&nbsp;<span style="margin-left: 1px;position: relative; top: 1px;">7天包退</span></li>
            <li v-show="yunfeiBol"><span style="font-size: 0.9rem;" class="iconfont icon-iconyunfeixian"></span>&nbsp;运费险</li>
            <li v-show="huandanBol"><span style="font-size: 0.8rem;" class="iconfont icon-pei"></span>&nbsp;换单包赔</li>
	        </ul>
	        <div class="el-icon-arrow-right"></div>
	      </li>
	      <li class="commodity-details">
	      	<div class="content">
	      		<p class="title">商品介绍</p>
	        	<p>{{nowGood.goodDetail.goodDesc}}</p>
	        </div>
	        <div class="more">
	        	<span class="el-icon-arrow-down"></span>
	        </div>
	      </li>
	    </ul>
	    <div class="commodity-service">
	      <span>服务：由亲亲宝贝商城发货并提供售后服务</span>
	    </div>
  	</div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: 'commodity',
    created () {
    },
    data () {
      return {
        swiperOption: {
          // autoplay: 4000,
          setWrapperSize: true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          mousewheelControl: true,
          observeParents: true,
          loop: true
        }
      }
    },
    computed: {
      isRight () {
        return this.$store.state.isRight
      },
      nowGood () {
        return this.$store.state.nowGood
      },
      baoyouBol () {
        return this.$store.state.nowGood.goodDetail.serviceType.some(item => item === '超值包邮')
      },
      baotuiBol () {
        return this.$store.state.nowGood.goodDetail.serviceType.some(item => item === '七天无理由')
      },
      shantuiBol () {
        return this.$store.state.nowGood.goodDetail.serviceType.some(item => item === '闪电退款')
      },
      yunfeiBol () {
        return this.$store.state.nowGood.goodDetail.serviceType.some(item => item === '运费险')
      },
      huandanBol () {
        return this.$store.state.nowGood.goodDetail.serviceType.some(item => item === '换单包赔')
      }
    }
  }
</script>

<style lang="less">
  #commodity {
    background-color: #F4F4F4;
    overflow: hidden;
    >#wrap{
      height: 100%;
    	overflow: scroll;
    	color: #333;
      .swiper-container,.swiper-wrapper {
        height: 12rem;
      }
      .swiper-pagination-bullet-active{
        background: rgb(255, 146, 146);
      }
      .swiper-slide {
        height: 100%;
        >img{
          width: 100%;
          height: 100%;
        }
      }
      .swiper-container {
        background-color: pink;
        margin-bottom: 0.55rem;
      }
		  .commodity-img {
		    width: 100%;
		    height: 14rem;
		    margin-bottom: 0.55rem;
		  }
		  .commodity-img img {
		    width: 100%;
		    height: 100%;
		  }
		  .commodity ul {
		    display: flex;
		    -webkit-justify-content: space-around;
		    justify-content: space-around;
		    -webkit-flex-wrap: wrap;
		    flex-wrap: wrap;
		    width: 100%;
		    margin-bottom: 0.5664rem;
		    list-style: none;
		  }
		  .commodity ul li {
		    width: 100%;
		  }
		  .commodity-header {
		    position: relative;
		    height: 4.02139844rem;
		    background-color: #FFF;
		    border-bottom: 1px solid #F4F4F4;
		  }
		  .commodity-name {
		    position: absolute;
		    top: 0.5rem;
		    left: 0.6rem;
		    font-size: 0.8rem;
		  }
		  .commodity-sale {
		    position: absolute;
		    left: 0.59375rem;
		    bottom: 0.338rem;
		    color: #E72B58;
		    font-size: 1.4rem;
		    font-weight: 600;
		    vertical-align: middle;
        .originalprice{
          color: #999;
          font-weight: normal;
          text-decoration: line-through;
        }
		  }
		  .commodity-sale sub {
		    font-size: 0.9rem;
		    vertical-align: bottom;
		  }
		  .commodity-posted {
		    border: 1px solid #E72B58;
		    padding: 0 0.2rem;
		    margin-left: 0.5rem;
		    font-style: normal;
		    font-size: 0.5rem;
		    font-weight: normal;
		  }
		  .commodity-support {
		    height: 2.5rem;
		    width: 100%;
		    background-color: #FFF;
		    border-bottom: 1px solid #F4F4F4;
		    position: relative;
		    >.el-icon-arrow-right{
					color: #E72B58;
					width: 10%;
			    position: absolute;
			    right: 0;
			    top: 0;
			    &:before{
						line-height: 2.5rem;
						font-size: 0.8rem;
			    }
		    }
		  }
		  .commodity-support ul {
		    height: 100%;
		    width: 90%;
		    white-space: nowrap;
		    overflow: scroll;
		  }
		  .commodity-support ul li {
		  	display: inline-block;
		    width: 32%;
		    height: 2.5rem;
		    line-height: 2.5rem;
		    font-size: 0.75rem;
		    text-align: center;
		    >.iconfont{
		    	color: #e72755;
		    	vertical-align: middle;
		    }
		  }
		  .commodity-details {
		    background-color: #FFF;
		    >.content{
		    	font-size: 0.75rem;
		    	text-indent: 2em;
		    	padding: 0 0.6rem;
          color: #666;
		    	display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 3;
					overflow: hidden;
					>.title{
						text-indent: 0;
						color: #333;
						padding-top: 0.4rem;
					}
		    }
		    >.more{
		    	.el-icon-arrow-down{
		    		text-align: center;
		    		color: #555;
		    	}
		    }
		  }
		  .commodity-details span {
		    display: -webkit-box;
		    margin: 0.5664rem 0.625rem 0;
		    -webkit-box-orient: vertical;
		    -webkit-line-clamp: 3;
		    text-overflow: ellipsis;
		    overflow: hidden;
		  }
		  .commodity-service {
		    height: 2.5rem;
		    background-color: #FFF;
		  }
		  .commodity-service span {
		    display: block;
		    height: 2.5rem;
		    margin-left: 0.6rem;
		    line-height: 2.8rem;
		    font-size: 0.75rem;
		  }
    }
  }
</style>



